#experience{
    height: max-content;
}

.skill_container{
    display:grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
   margin-bottom: 0;
   
}

.skill_container > div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}
.skill_container > div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.skill_container >div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.skill_content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 1.5rem;
}
.skill_details{
    display: flex;
    gap: 1rem;
}

@media screen and (max-width: 1024px){
    .skill_container{
        grid-template-columns: 1fr;
    }

    .skill_container > div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    
    }
    .skill_content{
        padding: 1rem;
    }
}

/* mobile view */
@media screen and (max-width: 600px){
.skill_container{
    gap: 1rem;

}

.skill_container > div{
    width: 92%;
    padding: 2rem 1rem;
}
}