header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header_container{
    text-align: center;
    height: 100%;
    position :relative;
}


/* CTA */

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/*Header  Socials */

.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0;
    position:absolute;
    left: 0;
    bottom: 3rem;
}

.header_socials::after{
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* profile image */

.me{
    background: linear-gradient( var(--color-primary),transparent);
    width: 22rem;
    height: 21rem;
    position:absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;

    border-radius: 12rem 12rem 0rem 0rem;
    overflow:visible;
    padding: 1.5rem ;


}



/* scroll down */
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;

}

/* tablet view  */
@media screen and (max-width:1024px){
    .header{
        height: 68vh;

    }
}

/* mobile view  */

@media screen and (max-width: 600px){
    .header{
        height: 100%;
    }
    

    .header_socials{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 0;
        position:absolute;
        left: 0;
        bottom: 1rem;
        margin-left: 3%;
    }
    .scroll_down{
        margin-right: 3%;
    }
    .header_container{
        text-align: center;
        height: 100%;
        position :relative;
    }
    
   
    
}